
    //MainStatus
.MainStatus{
    a{
        text-decoration: none;
        color:black;
    }
    width: 100%;
    height: 350px;
    .section__MainStatus{  
        height: 80%;
        margin:0 auto;
        margin-top:40px;
        border:1px solid blue;
        
        .section__MainStatus__box{
            
            .section__MainStatus__situation{
                margin:15px 20px;
            }

            .a{
                display:flex;
                align-items: center;
                justify-content: center;
                .section__MainStatus__information{
                    width: 150px;height: 150px;
                    border:1px solid black;
                    margin:14px 18px;
                }
                .section__MainStatus__menu{
                    ul{
                        li{
                            line-height: 22px;
                        }
                    }
                }
            }
            
            
            .section__MainStatus__btn{
                display:flex;;
                justify-content: center;
                div{
                    width: 35%; height: 35px;
                    border:1px solid black;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }
                div:nth-child(1){
                    margin-right:50px;
                }
            }
     }
    }
    
}
 /* MainFavorites */
 .MainFavorites{
     .section__favorites{
        width: 90%;
        height: 400px;
         border:1px solid black;
         margin:0 auto;
         .section__favorites__box{
             display:flex;
             div{
                 margin:0 auto;
                 border:1px solid black;
                 width: 100%;
                 text-align: center;
             }
             
         }
        
     }
     .section__favorites__box2{
         display:flex;
         margin-top:7px;
         justify-content: space-around;
        
     }
 }
 
 /* MainCarSearch */

 .MainCarSearch{
            margin-top:20px;
            width: 100%;
            
     a{
         text-decoration: none;
         color:black;
        
        }
     .MainCarSearch__box{
            display:grid;
            grid-template-columns: repeat(2,1fr);
            color:white;
            font-size:27px;
            background-color: rgb(57, 57, 224);
         .a{
             width: 60%;
             display:flex;
             margin:0 auto;
             margin-top:13px;
             align-items: center;
             div{
                 background-color: white;
                 width: 100%;
                 height: 40px;
                 border:1px solid black;
                 font-size:10px;
                margin-left:10px;
                font-weight: bold;
                display:flex;
                align-items: center;
                justify-content: center;       
             }
         }
         .MainCarSearch__box__charge{
             border:1px solid black;
             text-align: center;
             padding-top:20px;
             width: 100%; height: 100px;
            .a{
                div:nth-child(2){
                    height: 45px;
                    line-height: 14px;
                }
            }
            }
         .MainCarSearch__box__car{
            width: 100%; height: 100px;
             border:1px solid black;
             text-align: center;
             padding-top:20px;
         }
         .MainCarSearch__box__payment{
            width: 100%; height: 100px;
             text-align: center;
             border:1px solid black;
             padding-top:20px;

         }
         .MainCarSearch__box__use{
                width: 100%; height: 100px;

                border:1px solid black;
                text-align: center;
                padding-top:20px;
                line-height: 80px;   
             div{
                 a{
                     color:white;
                 }
             }
             
             
         }
     }
 }
 
 /* MainNotice */
 .MainNotice{
    a{
        text-decoration: none;
        color:black;
       
       }
     .MainNotice__box{
         display:flex;
         border:1px solid black;
         div:nth-child(1){
            width: 32%;
        }
         div{
             display:flex;
             justify-content: space-around;
             align-items: center;
             border:1px solid black;
             width: 100%;
             text-align: center;
             height: 52px;
             
         }
     }
 }

