.mycarheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .mycarheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .mycarheader__mycar{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}


main{
    a{
        color:black;
        text-decoration: none;
    }
    section{
        .mycar{
            width: 97%;
            height: 240px;
            border:1px solid blue;
            margin:0 auto;
            margin-top:20px;
            display:flex;
            .mycar__choice{
                width: 40px;
                height: 100%;
                border:1px solid blue;
                display:flex;
                flex-direction: column;
                justify-content: center;
                text-align: center;
            }
            .mycar__img{
                border:1px solid blue;
                width: 20%;
                height: 80%;
                margin:20px;
            }
            .mycar__text{
                width: 205px;
                margin:0 auto;
                display:flex;
                flex-direction: column;
                justify-content:center;
                p{
                    margin-bottom:10px;
                }
                div{
                    border:1px solid blue;
                    width: 90%;
                    height: 10%;
                    padding-top:5px;
                    margin: 0 auto;
                    position:relative;
                    top:28px;
                    text-align: center;
                }
            }
            .mycar__correction{
                width: 10%;
                display:flex;
                justify-content: center;
                align-items: center;
                border:1px solid blue;
            }
        }
        .mycar__registration{
            width: 100%;
            height: 40px;
            border:1px solid blue;
            position: fixed;
            bottom:0;
            text-align: center;
            line-height: 40px;
        }
    }
}