.header{
        nav{
        .header__ul{
            display:flex;
           
            border:1px solid black;
            height: 30px;
            li{
                border:1px solid black;
            }
            li:nth-child(1){
                width: 15%;   
                .menu__btn{
                    margin-top:12px;
                    position: relative;
                    z-index:1;
                    background-color: teal;
                    width: 300px;
                    height: 500px;
                    li{
                    
                    border:none;
                    }
                }
            }
            li:nth-child(2){
                width: 30%;
            }li:nth-child(3){
                width: 60%;
            }li:nth-child(4){
                width: 15%;
            }
        }
        }
    }
